import { useState, useEffect } from 'react'
import { useAuthStore } from '@store/authStore'

export const useAuth = () => {
  const { user, loading, login, logout, checkAuth } = useAuthStore()
  
  useEffect(() => {
    checkAuth()
  }, [checkAuth])

  return {
    user,
    loading,
    login,
    logout,
    isAuthenticated: !!user,
  }
}
