import { create } from 'zustand'

interface User {
  uid: string
  email?: string
  phone?: string
  name?: string
  role?: string
  isAdmin?: boolean
}

interface AuthState {
  user: User | null
  loading: boolean
  login: (credentials: any) => Promise<void>
  logout: () => Promise<void>
  checkAuth: () => Promise<void>
}

export const useAuthStore = create<AuthState>((set, get) => ({
  user: null,
  loading: true,
  
  login: async (credentials) => {
    set({ loading: true })
    try {
      // TODO: Implement actual login logic
      const mockUser = {
        uid: '1',
        email: credentials.email,
        name: 'Admin User',
        role: 'admin',
        isAdmin: true,
      }
      set({ user: mockUser, loading: false })
    } catch (error) {
      set({ loading: false })
      throw error
    }
  },
  
  logout: async () => {
    set({ loading: true })
    try {
      // TODO: Implement actual logout logic
      set({ user: null, loading: false })
    } catch (error) {
      set({ loading: false })
      throw error
    }
  },
  
  checkAuth: async () => {
    set({ loading: true })
    try {
      // TODO: Implement actual auth check
      const token = localStorage.getItem('token')
      if (token) {
        const mockUser = {
          uid: '1',
          email: 'admin@sitara777.com',
          name: 'Admin User',
          role: 'admin',
          isAdmin: true,
        }
        set({ user: mockUser, loading: false })
      } else {
        set({ user: null, loading: false })
      }
    } catch (error) {
      set({ user: null, loading: false })
    }
  },
}))
